/*
SCSS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: $icon-home-width;
}

The large array-like variables contain all information about a single icon
$icon-home: x y offset_x offset_y width height total_width total_height image_path;

At the bottom of this section, we provide information about the spritesheet itself
$spritesheet: width height image $spritesheet-sprites;
*/
$bear-name: 'bear';
$bear-x: 171px;
$bear-y: 0px;
$bear-offset-x: -171px;
$bear-offset-y: 0px;
$bear-width: 109px;
$bear-height: 106px;
$bear-total-width: 280px;
$bear-total-height: 279px;
$bear-image: 'sprites.png';
$bear: (171px, 0px, -171px, 0px, 109px, 106px, 280px, 279px, 'sprites.png', 'bear', );
$deflect-name: 'deflect';
$deflect-x: 171px;
$deflect-y: 116px;
$deflect-offset-x: -171px;
$deflect-offset-y: -116px;
$deflect-width: 46px;
$deflect-height: 46px;
$deflect-total-width: 280px;
$deflect-total-height: 279px;
$deflect-image: 'sprites.png';
$deflect: (171px, 116px, -171px, -116px, 46px, 46px, 280px, 279px, 'sprites.png', 'deflect', );
$dragon-name: 'dragon';
$dragon-x: 0px;
$dragon-y: 0px;
$dragon-offset-x: 0px;
$dragon-offset-y: 0px;
$dragon-width: 161px;
$dragon-height: 184px;
$dragon-total-width: 280px;
$dragon-total-height: 279px;
$dragon-image: 'sprites.png';
$dragon: (0px, 0px, 0px, 0px, 161px, 184px, 280px, 279px, 'sprites.png', 'dragon', );
$sun-name: 'sun';
$sun-x: 0px;
$sun-y: 194px;
$sun-offset-x: 0px;
$sun-offset-y: -194px;
$sun-width: 85px;
$sun-height: 85px;
$sun-total-width: 280px;
$sun-total-height: 279px;
$sun-image: 'sprites.png';
$sun: (0px, 194px, 0px, -194px, 85px, 85px, 280px, 279px, 'sprites.png', 'sun', );
$spritesheet-width: 280px;
$spritesheet-height: 279px;
$spritesheet-image: 'sprites.png';
$spritesheet-sprites: ($bear, $deflect, $dragon, $sun, );
$spritesheet: (280px, 279px, 'sprites.png', $spritesheet-sprites, );

/*
The provided mixins are intended to be used with the array-like variables

.icon-home {
  @include sprite-width($icon-home);
}

.icon-email {
  @include sprite($icon-email);
}
*/
@mixin sprite-width($sprite) {
  width: nth($sprite, 5);
}

@mixin sprite-height($sprite) {
  height: nth($sprite, 6);
}

@mixin sprite-position($sprite) {
  $sprite-offset-x: nth($sprite, 3);
  $sprite-offset-y: nth($sprite, 4);
  background-position: $sprite-offset-x  $sprite-offset-y;
}

@mixin sprite-image($sprite) {
  $sprite-image: nth($sprite, 9);
  background-image: url(#{$sprite-image});
}

@mixin sprite($sprite) {
  @include sprite-image($sprite);
  @include sprite-position($sprite);
  @include sprite-width($sprite);
  @include sprite-height($sprite);
}

/*
The `sprites` mixin generates identical output to the CSS template
  but can be overridden inside of SCSS

@include sprites($spritesheet-sprites);
*/
@mixin sprites($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite($sprite);
    }
  }
}
